.detail-cont .job-detail-head {
  border-bottom: 2px solid #EBEBEB;
  padding-bottom: 18px;
  font-size: 18px;
  font-weight: 500;
  color: #2f3130;
  line-height: 40px;
  margin-bottom: 10px;
}
.detail-cont .job-detail-head .header-left {
  text-align: left;
}
.detail-cont .job-detail-head .header-left .head-select .ant-select-selection {
  border: none;
  height: 30px;
  line-height: 30px;
}
.detail-cont .job-detail-head .header-left .head-select .ant-select-selection .ant-select-arrow {
  right: 8px;
  color: #000;
}
.detail-cont .job-detail-head .header-left .head-select .ant-select-selection .ant-select-selection__rendered {
  margin-left: 30px;
  line-height: 30px;
}
.detail-cont .job-detail-head .header-left .head-select .ant-select-selection .ant-select-selection-selected-value {
  font-size: 16px;
  font-weight: normal;
  color: #000000;
}
.detail-cont .job-detail-head .header-center {
  font-size: 16px;
  font-weight: 900;
  color: #0a78e6;
}
.detail-cont .job-detail-head .header-center .anticon-clock-circle {
  font-size: 24px;
  margin-right: 8px;
  font-weight: bold;
  vertical-align: text-bottom;
}
.detail-cont .job-detail-head .header-right {
  text-align: right;
}
.detail-cont .job-detail-head .header-right .right-btn {
  height: 40px;
  border-radius: 2px;
  border: 1px solid #0a78e6;
  line-height: 37px;
  width: 92px;
  font-size: 18px;
  font-weight: 300;
  color: #0a78e6;
  background: none;
  cursor: pointer;
}
.detail-cont .job-detail-tab .ant-tabs-top-bar .ant-tabs-nav-scroll {
  text-align: left;
}
.detail-cont .job-detail-tab .ant-tabs-top-bar .ant-tabs-nav-container {
  border-bottom: 1px solid #EBEBEB;
}
.detail-cont .job-detail-tab .ant-tabs-top-bar .ant-tabs-tab {
  font-size: 18px;
  font-weight: normal;
  color: #2f3130;
  padding: 7px 0px;
  margin-right: 50px;
}
.detail-cont .job-detail-tab .ant-tabs-top-bar .ant-tabs-tab.ant-tabs-tab-active {
  color: #0A78E6;
  border-bottom: #0A78E6 3px solid;
  height: auto;
}
.detail-cont .job-detail-tab .job-detail-main {
  margin-top: 15px;
  text-align: left;
}
.detail-cont .job-detail-tab .job-detail-main .main-box {
  padding: 50px 60px;
  border: 1px solid #D5D7D8;
}
.detail-cont .job-detail-tab .job-detail-main .main-img {
  width: 100%;
}
.detail-cont .job-detail-tab .job-detail-main .main-activity .main-left-list {
  margin-bottom: 20px;
}
.detail-cont .job-detail-tab .job-detail-main .main-activity .main-left-list .main-left-name {
  font-size: 16px;
  font-weight: bold;
  color: #2f3130;
  margin-bottom: 10px;
}
.detail-cont .job-detail-tab .job-detail-main .main-activity .main-left-list .main-left-name .name-doc {
  font-size: 14px;
  font-weight: 300;
  color: #505253;
}
.detail-cont .job-detail-tab .job-detail-main .main-activity .main-left {
  background: #F3F6F7;
  padding: 20px;
  height: 100%;
}
.detail-cont .job-detail-tab .job-detail-main .main-activity .main-left .main-left-info {
  font-size: 14px;
  font-weight: 300;
  color: #505253;
  line-height: 28px;
}
.detail-cont .job-detail-tab .job-detail-main .main-activity .main-left .main-left-ul li {
  font-size: 14px;
  font-weight: 300;
  color: #505253;
  line-height: 28px;
  position: relative;
  padding-left: 18px;
}
.detail-cont .job-detail-tab .job-detail-main .main-activity .main-left .main-left-ul li:before {
  content: '';
  position: absolute;
  left: 0;
  top: 50%;
  transform: translateY(-50%);
  width: 6px;
  height: 6px;
  border-radius: 50%;
  background: #EE8808;
}
.detail-cont .job-detail-tab .job-detail-main .main-activity .main-right {
  padding: 20px 0;
}
.detail-cont .job-detail-tab .job-detail-main .main-activity .main-right .upload-video .ant-btn {
  width: 178px;
  height: 100px;
  border-radius: 5px !important;
  border: 1px solid #e6e6e6;
  color: #D8D8D8;
  font-size: 30px;
}
.detail-cont .job-detail-tab .job-detail-main .main-activity .main-right .upload-audio .ant-btn {
  width: 191px;
  height: 44px;
  border-radius: 22px !important;
  border: 1px solid #d8d8d8;
  font-size: 18px;
  font-weight: 500;
  color: #a4a6a6;
}
.detail-cont .job-detail-tab .job-detail-main .main-activity .main-right .upload-audio .audio_box {
  display: flex;
  align-items: center;
}
.detail-cont .job-detail-tab .job-detail-main .main-activity .main-right .upload-audio .audio_box audio {
  border-radius: 30px;
  border: 1px solid #0A78E6;
}
.detail-cont .job-detail-tab .job-detail-main .main-activity .main-right .upload-audio .audio_box .anticon-close-circle {
  line-height: 54px;
  margin-left: 10px;
  color: #D8D8D8;
  font-size: 26px;
  cursor: pointer;
}
.detail-cont .job-detail-tab .job-detail-main .main-activity .main-right .main-right-footer {
  border-top: 1px solid #EBEBEB;
  text-align: right;
}
.detail-cont .job-detail-tab .job-detail-main .main-activity .main-right .main-right-footer .job-submit {
  width: 140px;
  height: 44px;
  line-height: 40px;
  background: #0a78e6;
  border-radius: 2px;
  font-size: 18px;
  font-weight: bold;
  color: #ffffff;
  border: none;
  margin-top: 12px;
}
