* {
  font-family: 'PingFang SC', 'Helvetica Neue', 'Microsoft YaHei UI', 'Microsoft YaHei', 'Noto Sans CJK SC', Sathu, EucrosiaUPC, Arial, Helvetica, sans-serif;
}
.mb20 {
  margin-bottom: 20px;
}
.mb40 {
  margin-bottom: 40px;
}
.st-warp {
  min-height: 100%;
  box-sizing: border-box;
  position: relative;
  width: 100%;
  background: url("../assets/students/bg_page.png") no-repeat center center;
  background-size: 100% 100%;
}
.st-warp .st-cont {
  max-width: 1054px;
  width: 100%;
  margin: 0 auto;
  padding: 90px 0px 60px;
}
/*tab菜单start*/
.mycourse-tab {
  box-sizing: border-box;
  border-bottom: 1px solid #ddd;
  position: relative;
}
.mycourse-tab .tab-badeg {
  height: 18px;
  background: #0a78e6;
  border-radius: 2px;
  font-size: 12px;
  font-weight: bold;
  padding: 0 10px;
  margin-left: 5px;
  color: #ffffff;
  line-height: 18px;
}
.mycourse-tab .ant-radio-button-wrapper {
  border: none;
  margin: 0 30px 0 0;
  font-size: 18px;
  height: 38px;
}
.mycourse-tab .ant-radio-button-wrapper:not(:first-child)::before {
  position: absolute;
  top: 0;
  left: -1px;
  display: block;
  width: 0px;
  height: 100%;
  background-color: #d9d9d9;
  content: '';
}
.mycourse-tab .ant-radio-group {
  text-align: left;
}
.mycourse-tab .ant-radio-group-solid .ant-radio-button-wrapper-checked:not(.ant-radio-button-wrapper-disabled) {
  color: #0A78E6;
  background: #fff;
  border-bottom: 2px solid #0A78E6;
  border-radius: 0;
  box-shadow: none;
}
/*tab菜单end*/
/*首页作业列表start*/
.stlist-box .stlist-item {
  display: flex;
  align-items: center;
  padding: 10px 0;
}
.stlist-box .stlist-item .stlist-center {
  width: calc(100% - 150px);
  margin-left: 15px;
}
.stlist-box .stlist-item .stlist-center .st-name {
  height: 20px;
  font-size: 14px;
  font-weight: 500;
  color: #2f3130;
  line-height: 20px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  margin-bottom: 0px;
}
.stlist-box .stlist-item .stlist-center .st-name .st-number {
  color: #DF4885;
}
.stlist-box .stlist-item .stlist-center .st-date {
  font-size: 12px;
  font-weight: 300;
  color: #505253;
  line-height: 18px;
  margin-bottom: 0;
}
.stlist-box .stlist-item .stlist-left {
  width: 48px;
  height: 48px;
  background: #0a78e6;
  font-size: 14px;
  font-weight: 900;
  line-height: 32px;
  text-align: center;
  border-radius: 50%;
  color: #fff;
  padding: 8px;
  box-sizing: border-box;
}
.stlist-box .stlist-item .stlist-right {
  width: 70px;
  position: relative;
  font-size: 12px;
  font-weight: 300;
  color: #0a78e6;
  line-height: 18px;
}
.stlist-box .stlist-item .stlist-right .st-time {
  height: 20px;
  font-size: 14px;
  font-weight: 400;
  color: #000000;
  line-height: 20px;
  margin-bottom: 0;
}
.stlist-box .stlist-item .stlist-right .ant-progress-show-info .ant-progress-outer {
  margin-right: 0;
  padding-right: 0;
}
.stlist-box .stlist-item .stlist-right .ant-progress-text {
  position: absolute;
  right: 0;
  top: -19px;
  font-size: 14px;
  font-weight: 400;
  color: #000000;
  line-height: 20px;
}
/*作业列表end*/
/*错题本列表star*/
.question-box .question-title {
  color: #000;
  font-weight: 600;
  margin: 20px 0;
}
.question-box .question-title .question-num {
  font-size: 18px;
  color: #0a78e6;
  margin-right: 5px;
}
.question-box .select-num {
  font-size: 18px;
  font-weight: 500;
  color: #0a78e6;
  margin-right: 10px;
}
.question-box .ant-radio-wrapper {
  display: block;
  min-width: 239px;
  max-width: 85%;
  height: 48px;
  line-height: 48px;
  background: #ffffff;
  border-radius: 2px;
  border: 1px solid #d5d7d8;
  margin-bottom: 10px;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.question-box .ant-radio-inner {
  display: none;
}
.question-box .ant-radio-wrapper-checked {
  border: 2px solid #0A78E6;
}
.question-box .question-bottom {
  float: right;
}
.question-box .question-bottom .next-question {
  width: 140px;
  height: 44px;
  line-height: 44px;
  background: #0A78E6;
  background-size: 27px 20px;
  border-radius: 2px;
  color: #fff;
  position: relative;
  box-sizing: border-box;
  padding-left: 35px;
  cursor: pointer;
}
.question-box .question-bottom .next-question:after {
  position: absolute;
  content: "";
  background: url(../assets/students/right.png) no-repeat;
  background-size: 17px 20px;
  width: 17px;
  height: 20px;
  right: 15px;
  top: 12px;
}
.question-box .question-bottom .complete {
  background: #d5d7d8;
}
.question-box .ant-radio-group {
  margin-bottom: 140px;
}
/*错题本列表end*/
/*我的作业start*/
.job-list-box .ant-col:nth-last-child(1) .stlist-item,
.job-list-box .ant-col:nth-last-child(2) .stlist-item {
  border-bottom: none !important;
}
.job-list-box {
  padding-top: 20px;
}
.job-list-box.completed-box .stlist-left {
  background: #0BA140;
}
.job-list-box .stlist-item {
  padding: 24px 18px;
  border-bottom: 1px solid #EBEBEB;
}
.job-list-box .stlist-item.state-Active .stlist-left {
  background: #DF4885;
}
.job-list-box .stlist-item.state-Active .percentage {
  color: #DF4885 !important;
}
.job-list-box .stlist-item.state-Active-or .stlist-left {
  background: #EE8808;
}
.job-list-box .stlist-item.state-Active-or .percentage {
  color: #EE8808 !important;
}
.job-list-box .stlist-item .stlist-left {
  width: 64px;
  height: 64px;
  line-height: 48px;
  font-size: 18px;
}
.job-list-box .stlist-item .stlist-center {
  margin: 0 10px;
  width: calc(100% - 154px);
}
.job-list-box .stlist-item .stlist-center .st-job-info {
  font-size: 14px;
  color: #505253;
  margin-bottom: 0px;
}
.job-list-box .stlist-item .stlist-center .st-date {
  font-size: 14px;
}
.job-list-box .stlist-item .stlist-center .st-date .job-people-num {
  color: #0A78E6;
}
.job-list-box .stlist-item .stlist-center .st-name {
  font-size: 16px;
}
.job-list-box .stlist-item .stlist-right.completed {
  text-align: right;
}
.job-list-box .stlist-item .stlist-right .ant-progress-text {
  color: #0A78E6;
}
.job-list-box .stlist-item .stlist-right .stlist-state {
  color: #2F3130;
  margin-right: 2px;
}
.job-list-box .stlist-item .stlist-right .stlist-state-box {
  height: 36px;
}
.job-list-box .stlist-item .stlist-right .stlist-state-box .percentage {
  color: #0BA140;
  font-size: 24px;
  margin-bottom: 0;
}
.job-list-box .stlist-item .stlist-right .stlist-time {
  margin-top: 6px;
}
.job-list-box .stlist-item .stlist-right .stlist-time .st-time {
  color: #0A78E6;
}
.job-list-box .stlist-item .stlist-right .stlist-time .icondelayed {
  width: 60px;
}
/*我的作业end*/
/*抽屉弹层start*/
.screen-box .drawer-footer {
  text-align: right;
  border-top: 1px solid #EBEBEB;
  padding-top: 10px;
  display: flex;
  justify-content: flex-end;
  align-items: center;
}
.screen-box .drawer-footer .ant-btn {
  margin-left: 20px;
  height: 44px;
  border-radius: 2px !important;
  border: 1px solid #0a78e6;
  padding: 0 20px;
  min-width: 140px;
  line-height: 44px;
  color: #0A78E6;
  font-size: 18px;
}
.screen-box .drawer-footer .ant-btn.drawer-submit {
  background: #0A78E6 !important;
  color: #fff;
}
.screen-box .ant-drawer-right.ant-drawer-open .ant-drawer-content-wrapper {
  width: 600px!important;
}
.screen-box .ant-drawer-body {
  padding: 0 20px 30px;
}
.screen-box .site-form-in-drawer-wrapper {
  position: absolute;
  right: 0px;
  bottom: 0px;
  width: 100%;
  padding: 10px 16px;
  border-top: 1px solid #e9e9e9;
  background: #fff;
  text-align: right;
}
.screen-box .knowledge-box.drawer-path {
  display: flex;
  margin: 20px 0;
}
.screen-box .knowledge-box.drawer-path .drawer-book {
  width: 36px;
  margin-right: 20px;
  height: 50px;
}
.screen-box .knowledge-box.drawer-path .knowledge-name {
  color: #2F3130;
  font-size: 18px;
  border-bottom: 1px solid #EBEBEB;
  padding-bottom: 5px;
  margin-bottom: 10px;
  font-weight: bold;
}
.screen-box .knowledge-box.drawer-path .knowledge-name .book_come {
  font-size: 16px;
  color: #505253;
  font-weight: normal;
}
.screen-box .knowledge-box.drawer-path .drawer-right {
  width: calc(100% - 56px);
}
.screen-box .knowledge-box.drawer-path .drawer-right .ant-radio-wrapper {
  height: 44px;
  border-radius: 2px;
  border: 1px solid #d5d7d8;
  line-height: 40px;
  padding: 0 15px;
  font-size: 22px;
  font-weight: 400;
  color: #505253;
  margin-bottom: 20px;
}
.screen-box .knowledge-box.drawer-path .drawer-right .ant-radio-wrapper.ant-radio-wrapper-checked {
  background: #0A78E6;
  color: #fff;
}
.screen-box .knowledge-box .knowledge-tit {
  font-size: 16px;
  font-weight: bold;
  color: #2f3031;
  margin: 15px 0;
}
.screen-box .knowledge-box .ant-radio-wrapper {
  display: inline-block;
  height: 22px;
  line-height: 22px;
  background: #ffffff;
  border-radius: 2px;
  margin-bottom: 10px;
  text-overflow: ellipsis;
  white-space: nowrap;
  min-width: 54px;
  text-align: center;
}
.screen-box .knowledge-box .ant-radio-inner {
  display: none;
}
.screen-box .knowledge-box .ant-radio-wrapper-checked {
  border-radius: 3px;
  color: #fff;
  background: #06a5d8;
}
/*抽屉弹层end*/
